export function getButtonVariants({ variant = 'default', size = 'default', className = '' }) {
	return `vanilla-button vanilla-button--variant-${variant} vanilla-button--size-${size} ${className}`.trim();
}

export class Button extends HTMLElement {
	connectedCallback() {
		const variant = this.getAttribute('variant') || 'default';
		const size = this.getAttribute('size') || 'default';
		const userClass = this.getAttribute('class') || '';
		const href = this.getAttribute('href');

		// Create either a button or anchor element based on the presence of href
		const element = href ? document.createElement('a') : document.createElement('button');

		// Use the getButtonClassNames function to generate the class names
		element.className = getButtonVariants({ variant, size, className: userClass });

		element.innerHTML = this.innerHTML;

		// Apply all attributes from the custom element to the element
		for (const attr of this.attributes) {
			if (attr.name !== 'variant' && attr.name !== 'size' && attr.name !== 'class') {
				element.setAttribute(attr.name, attr.value);
			}
		}

		// If it's an anchor, ensure it has the href attribute
		if (href) {
			element.setAttribute('href', href);
		}

		// Replace the custom element with the newly created element
		this.replaceWith(element);
	}
}
